
"use client";

import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { z } from "zod";
import { Button } from "@/components/ui/button";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter, DialogDescription } from "@/components/ui/dialog";
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import { Patient, users } from "@/lib/constants";
import { useEffect, useRef, useState } from "react";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "./ui/select";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Camera, Upload, User as UserIcon, X, RotateCcw } from "lucide-react";
import Image from "next/image";

const formSchema = z.object({
  name: z.string().min(2, "Name must be at least 2 characters."),
  gender: z.enum(['Male', 'Female']),
  condition: z.string().min(2, "Condition must be at least 2 characters."),
  assignedDoctor: z.string().min(1, "Please assign a doctor."),
  avatarUrl: z.string().optional(),
});

type FormData = z.infer<typeof formSchema>;
type PatientSaveData = Omit<Patient, 'id' | 'medicalHistory' | 'prescriptions' | 'lastVisit' | 'labTests' | 'bloodType'>;

interface CreatePatientDialogProps {
  isOpen: boolean;
  onClose: () => void;
  onPatientSaved: (patient: PatientSaveData) => void;
}

const doctors = users.filter(u => u.role === 'doctor');

export function CreatePatientDialog({ isOpen, onClose, onPatientSaved }: CreatePatientDialogProps) {
    const [hasCameraPermission, setHasCameraPermission] = useState<boolean | null>(null);
    const [photoPreview, setPhotoPreview] = useState<string | null>(null);
    const [activeTab, setActiveTab] = useState("camera");
    const videoRef = useRef<HTMLVideoElement>(null);
    const canvasRef = useRef<HTMLCanvasElement>(null);
    const fileInputRef = useRef<HTMLInputElement>(null);

    const form = useForm<FormData>({
        resolver: zodResolver(formSchema),
        defaultValues: {
            name: "",
            gender: "Male",
            condition: "Stable",
            assignedDoctor: "",
            avatarUrl: "",
        },
    });

    const resetState = () => {
        form.reset({
            name: "",
            gender: "Male",
            condition: "Stable",
            assignedDoctor: doctors.length > 0 ? doctors[0].name : "",
            avatarUrl: "",
        });
        setPhotoPreview(null);
        setHasCameraPermission(null);
        if (fileInputRef.current) fileInputRef.current.value = "";
    }

    const startVideoStream = async () => {
        try {
            if (videoRef.current && videoRef.current.srcObject) {
                const stream = videoRef.current.srcObject as MediaStream;
                stream.getTracks().forEach(track => track.stop());
            }
            const stream = await navigator.mediaDevices.getUserMedia({ video: true });
            setHasCameraPermission(true);
            if (videoRef.current) {
                videoRef.current.srcObject = stream;
            }
        } catch (error) {
            console.error('Error accessing camera:', error);
            setHasCameraPermission(false);
        }
    };

    const stopVideoStream = () => {
        if (videoRef.current && videoRef.current.srcObject) {
            const stream = videoRef.current.srcObject as MediaStream;
            stream.getTracks().forEach(track => track.stop());
            videoRef.current.srcObject = null;
        }
    };
    
    useEffect(() => {
        if (isOpen) {
            resetState();
            if (activeTab === 'camera') {
                startVideoStream();
            }
        } else {
            stopVideoStream();
        }
    }, [isOpen]);

    useEffect(() => {
        if (isOpen) {
            if (activeTab === 'camera') {
                startVideoStream();
            } else {
                stopVideoStream();
            }
        }
    }, [activeTab, isOpen]);


    const handleCapture = () => {
        if (videoRef.current && canvasRef.current) {
            const video = videoRef.current;
            const canvas = canvasRef.current;
            canvas.width = video.videoWidth;
            canvas.height = video.videoHeight;
            const context = canvas.getContext('2d');
            context?.drawImage(video, 0, 0, video.videoWidth, video.videoHeight);
            const dataUrl = canvas.toDataURL('image/png');
            setPhotoPreview(dataUrl);
            form.setValue('avatarUrl', dataUrl);
        }
    }
    
    const handleFileChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        const file = event.target.files?.[0];
        if (file) {
            const reader = new FileReader();
            reader.onloadend = () => {
                const dataUrl = reader.result as string;
                setPhotoPreview(dataUrl);
                form.setValue('avatarUrl', dataUrl);
            }
            reader.readAsDataURL(file);
        }
    }

    const clearPhoto = () => {
        setPhotoPreview(null);
        form.setValue('avatarUrl', undefined);
        if (fileInputRef.current) {
            fileInputRef.current.value = "";
        }
    }

    function onSubmit(values: FormData) {
        onPatientSaved(values);
    }

    return (
        <Dialog open={isOpen} onOpenChange={onClose}>
            <DialogContent className="max-w-4xl">
                <DialogHeader>
                    <DialogTitle>Create New Patient</DialogTitle>
                    <DialogDescription>
                        Fill in the details and add a photo to register a new patient.
                    </DialogDescription>
                </DialogHeader>
                <Form {...form}>
                    <form onSubmit={form.handleSubmit(onSubmit)} className="grid md:grid-cols-2 gap-x-8 gap-y-6">
                        <div className="space-y-4">
                            <FormField
                                control={form.control}
                                name="name"
                                render={({ field }) => (
                                    <FormItem>
                                        <FormLabel>Full Name</FormLabel>
                                        <FormControl><Input placeholder="Jane Smith" {...field} /></FormControl>
                                        <FormMessage />
                                    </FormItem>
                                )}
                            />
                             <div className="grid grid-cols-2 gap-4">
                                <FormField
                                    control={form.control}
                                    name="gender"
                                    render={({ field }) => (
                                        <FormItem>
                                            <FormLabel>Gender</FormLabel>
                                            <Select onValueChange={field.onChange} defaultValue={field.value}>
                                                <FormControl><SelectTrigger><SelectValue placeholder="Select gender" /></SelectTrigger></FormControl>
                                                <SelectContent>
                                                    <SelectItem value="Male">Male</SelectItem>
                                                    <SelectItem value="Female">Female</SelectItem>
                                                </SelectContent>
                                            </Select>
                                            <FormMessage />
                                        </FormItem>
                                    )}
                                />
                                 <FormField
                                    control={form.control}
                                    name="condition"
                                    render={({ field }) => (
                                        <FormItem>
                                            <FormLabel>Initial Condition</FormLabel>
                                            <FormControl><Input placeholder="e.g., Stable, Critical" {...field} /></FormControl>
                                            <FormMessage />
                                        </FormItem>
                                    )}
                                />
                            </div>
                            <FormField
                                control={form.control}
                                name="assignedDoctor"
                                render={({ field }) => (
                                    <FormItem>
                                        <FormLabel>Assign Doctor</FormLabel>
                                        <Select onValueChange={field.onChange} defaultValue={field.value}>
                                            <FormControl><SelectTrigger><SelectValue placeholder="Select a doctor" /></SelectTrigger></FormControl>
                                            <SelectContent>
                                                {doctors.map((doctor) => (<SelectItem key={doctor.email} value={doctor.name}>{doctor.name}</SelectItem>))}
                                            </SelectContent>
                                        </Select>
                                        <FormMessage />
                                    </FormItem>
                                )}
                            />
                        </div>
                        <div className="space-y-4">
                           <FormLabel>Patient Photo</FormLabel>
                             <div className="aspect-video w-full rounded-md border bg-muted flex items-center justify-center overflow-hidden relative group/photo">
                                {photoPreview ? (
                                    <>
                                        <Image src={photoPreview} alt="Patient preview" layout="fill" objectFit="cover" />
                                        <div className="absolute inset-0 bg-black/40 flex items-center justify-center opacity-0 group-hover/photo:opacity-100 transition-opacity">
                                            <Button type="button" size="icon" variant="destructive" onClick={clearPhoto}>
                                                <X className="h-5 w-5" />
                                                <span className="sr-only">Clear Photo</span>
                                            </Button>
                                        </div>
                                    </>
                                ) : (
                                    <div className="text-center text-muted-foreground p-4">
                                        <UserIcon className="mx-auto h-12 w-12" />
                                        <p>No photo selected</p>
                                    </div>
                                )}
                             </div>
                            <Tabs value={activeTab} onValueChange={setActiveTab} className="w-full">
                                <TabsList className="grid w-full grid-cols-2">
                                    <TabsTrigger value="camera"><Camera className="mr-2 h-4 w-4"/>Take Photo</TabsTrigger>
                                    <TabsTrigger value="upload"><Upload className="mr-2 h-4 w-4"/>Upload</TabsTrigger>
                                </TabsList>
                                <TabsContent value="camera" className="p-4 border border-t-0 rounded-b-md">
                                    <div className="aspect-video w-full rounded-md bg-muted flex items-center justify-center overflow-hidden">
                                        {hasCameraPermission === false ? (
                                            <div className="text-center text-muted-foreground p-4">
                                                <Camera className="mx-auto h-10 w-10 mb-2"/>
                                                <p className="font-semibold">Camera access denied.</p>
                                            </div>
                                        ) : (
                                            <video ref={videoRef} className="w-full h-full object-cover" autoPlay muted playsInline />
                                        )}
                                    </div>
                                    <Button type="button" className="w-full mt-4" onClick={handleCapture} disabled={!hasCameraPermission}>
                                        <Camera className="mr-2 h-4 w-4" /> Capture
                                    </Button>
                                    <canvas ref={canvasRef} className="hidden" />
                                </TabsContent>
                                <TabsContent value="upload" className="p-4 border border-t-0 rounded-b-md">
                                     <div className="flex items-center justify-center w-full">
                                        <label htmlFor="file-upload" className="flex flex-col items-center justify-center w-full h-40 border-2 border-dashed rounded-lg cursor-pointer hover:bg-muted">
                                            <div className="flex flex-col items-center justify-center pt-5 pb-6">
                                                <Upload className="w-8 h-8 mb-4 text-muted-foreground" />
                                                <p className="mb-2 text-sm text-muted-foreground"><span className="font-semibold">Click to upload</span> or drag and drop</p>
                                                <p className="text-xs text-muted-foreground">PNG, JPG, or GIF (MAX. 800x400px)</p>
                                            </div>
                                            <Input id="file-upload" type="file" className="hidden" accept="image/*" ref={fileInputRef} onChange={handleFileChange} />
                                        </label>
                                    </div> 
                                </TabsContent>
                            </Tabs>
                        </div>
                        <div className="md:col-span-2">
                             <DialogFooter>
                                <Button type="button" variant="ghost" onClick={onClose}>Cancel</Button>
                                <Button type="submit">Create Patient</Button>
                            </DialogFooter>
                        </div>
                    </form>
                </Form>
            </DialogContent>
        </Dialog>
    );
}
